* {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

body {
  background-color: rgb(238, 236, 236);
  font-family: "Roboto", sans-serif;
  line-height: 1;
  max-width: 100%;
  overflow-x: hidden;
}

.container {
  margin-left: 280px;
  transition: margin-left 0.2s ease-in;
}

.container.inactive {
  margin-left: 100px;
}
