* {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

body {
  background-color: rgb(238, 236, 236);
  font-family: "Roboto", sans-serif;
  line-height: 1;
  max-width: 100%;
  overflow-x: hidden;
}

.container {
  margin-left: 280px;
  transition: margin-left 0.2s ease-in;
}

.container.inactive {
  margin-left: 100px;
}

.td1 {
  float: left;
  width: 25%;
  height: 50px;
}

.td1.data {
  padding-left: 1rem;
  padding-top: 1rem;
}

/* google fonts */

body .auth-page {
  margin: 20px 40px;
  font-size: 1.2rem;
  letter-spacing: 1px;
  background: #fafafa;
}
.auth-page h1,
.auth-page h2,
.auth-page h3,
.auth-page h4,
.auth-page ul,
.auth-page li,
.auth-page a,
.auth-page p,
.auth-page input,
.auth-page label,
.auth-page button,
.auth-page div,
.auth-page footer {
  margin: 0;
  padding: 0;
  font-family: "Quicksand", sans-serif;
  color: #444;
}
.auth-page ul {
  list-style-type: none;
}
.auth-page a {
  text-decoration: none;
}
.auth-page nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  margin-bottom: 110px;
}
.auth-page nav ul {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.auth-page nav li {
  margin-left: 20px;
}
.auth-page nav li a {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 0.8em;
  display: block;
  padding: 10px 16px;
  letter-spacing: 2px;
  float: right;
}
.auth-page .btn {
  border-radius: 36px;
  background: #fee996;
}
.auth-page form h2 {
  font-size: 2.4em;
  font-weight: 900;
  margin-bottom: 40px;
}
.auth-page form {
  width: 360px;
  margin: 0 auto;
  padding: 30px;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  background: white;
}
.auth-page input {
  padding: 10px 12px;
  border-radius: 4px;
  border: 1px solid #ddd;
  font-size: 1em;
  width: 100%;
}
.auth-page label {
  display: block;
  margin: 20px 0 10px;
}
.auth-page button {
  margin-top: 30px;
  border-radius: 36px;
  background: #fee996;
  border: 0;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 0.8em;
  display: block;
  padding: 10px 16px;
  letter-spacing: 2px;
}
.auth-page .error {
  color: #ff0099;
  margin: 10px 2px;
  font-size: 0.8em;
  font-weight: bold;
}
.auth-page header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.auth-page header img {
  width: 250px;
  margin-right: 40px;
}
.auth-page header h2 {
  font-size: 3em;
  margin-bottom: 10px;
}
.auth-page header h3 {
  font-size: 1.6em;
  margin-bottom: 10px;
  margin-left: 2px;
  color: #999;
}
.auth-page header .btn {
  margin-top: 20px;
  padding: 12px 18px;
  text-transform: uppercase;
  font-weight: bold;
  display: inline-block;
  font-size: 0.8em;
}

.auth-page footer {
  text-align: center;
  margin-top: 120px;
  color: #aaa;
}

.sidebar {
  position: fixed;
  background-color: #2e3650;

  height: 100% !important;
  width: 270px;
  z-index: 100;
  padding: 30px 30px;
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  transition: width 0.2s ease-in;
}

.sidebar.inactive {
  width: 90px;
}

