.top-section {
  position: relative;
}

.sidebar-dashboard {
  height: 60px;
  width: 60px;
  overflow: hidden;
  display: inline-block;
}

.sidebar-dashboard i {
  max-width: 100%;
  max-height: 100%;
  font-size: 30px;
  display: inline-block;
  color: white;
}

.sidebar.inactive .toggle-arrow i {
  right: -60px;
}

.toggle-arrow i {
  color: slateblue;
  font-size: 23px;
  display: inline-block;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
}

.search-box i {
  display: inline-block;
  padding-right: 25px;
  position: absolute;
  border: 0;
  top: 15.5%;
  -webkit-transform: translateY(-38%);
          transform: translateY(-38%);
  left: 40px;
}

.search-box input {
  border: 0;
  outline: none;
  height: 30px;
  width: 100%;
  border-radius: 5px;
  display: block;
  margin: 10px 0;
  box-sizing: border-box;
  padding-left: 35px;
}

.divider {
  width: 100%;
  height: 1px;
  border-radius: 2px;
  background-color: white;
  margin: 15px 0;
}

.menu-item,
.menu-item:hover {
  text-decoration: none;
  font-size: 15px;
  display: inline-block;
  font-weight: 400;
  color: white;
  padding-bottom: 15px;
  cursor: pointer;
}

.menu-item.active {
  background: #384779;
  width: 100%;
  border-radius: 5px;
}
.menu-item span {
  position: absolute;
  display: inline-block;
  line-height: 50px;
  opacity: 1;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out;
  /* opacity 0.2s ease-in; */
}

.sidebar.inactive .main-menu .menu-item span {
  opacity: 0;
  width: 0;
  height: 0;
  overflow: hidden;
}

.menu-icon {
  display: inline-block;
  font-size: 15px;
  font-weight: 700;
  height: 30px;
  width: 30px;
  line-height: 50px;
  text-align: center;
}

.sub-menu {
  text-decoration: none;
  font-size: 15px;
  font-weight: 400;
  color: white;
  margin-left: 15px;
  border-left: 1px solid;
  box-sizing: border-box;
  padding-left: 25px;
  cursor: pointer;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-in;
}

.sub-menu a {
  display: block;
  margin: 5px 0;
  padding-bottom: 10px;
  text-decoration: none;
  color: white;
  box-sizing: border-box;
  padding: 5px 3px;
}

.sub-menu a.active {
  background: #384779;
  width: 100%;
  border-radius: 5px;
  color: white;
  display: block;
}

.sub-menu.active {
  max-height: 200px;
}

.footer {
  width: 100%;
  height: 80px;
  border-radius: 2px;
  background: #384779;
  bottom: 0;
  left: 0;
  position: absolute;
  box-sizing: border-box;
}

.avatar {
  font-size: 40px;
  padding: 25px;
  display: inline-block;
}

.user-info {
  display: inline-block;
  color: white;
}
